#tile {
    background: pink;
    height: 300px;
    width: 300px;
}

#board {
    background: gray;
    height: 256px;
    width: 256px;
    align-items: center;
    float: none;
    display: flex;
    justify-content: center;
}

#reset {
    background: lightpink;
    height: 30px;
    width: 100px;
    border: solid gray 2px;
}

#counter {
    background: gray;
    font-size: 20px;
    color: white;
    padding: 10px;
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
}

#menu {
    display: flex;
    width: 100%;
    padding: 5px;
}

.cell {
    border: 1px solid black;
    height: 50px;
    width: 50px;
    display: flex;
    font-size: 25px;
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;      
}

.row {
    display: inline-block;
    vertical-align: top
}

body {
    background: black;
    align-items: center;
    float: none;
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

#level {
    padding-left: 50px;
    justify-content: center;
    align-items: center;
    display: flex;

}

button {
    transition-duration: 0.4s;
    
    
}
      
button:hover {
    background-color: lightslategray; /* Green */
    color: white;
}

#title {
    color: white;
    width: 100%;
    text-align: center;
    font-size: 50px;
    font-family: "Arial Black", Gadget, sans-serif;
    
}

#link {
    color: white;
    width: 100%;
    text-align: center;
    font-size: 10px;
    font-family: "Arial", Gadget, sans-serif;
    padding-bottom: 20px;
}

a {
    color: white;
}

a:hover {
    color: gray;
}

#rules {
    color: white;
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-family: "Arial", Gadget, sans-serif;
    padding-top: 20px;
}
